import { BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import Page1 from './Page1';
const Reservation = () => {
    return ( 
        <Router>
      <div className="App">
        
        <div className="content">
          {/* Surround routes with a switch to only have 1 page */}
          <Switch>
            {/* <Route exact path="/"> */}
              <Page1/>
            {/* </Route> */}

            
            
          </Switch>
        </div>
                  
      </div>
    </Router>
     );
}
 
export default Reservation;